#root {
    width: 100%;
    height: 100%;
    // min-width: 1250px;

    .state-box {
        width: 100%;
        background: #f5f5f5;
        overflow: hidden;
        position: relative;

        >.state-box-con {
            width: 100%;
            height: 50px;

            >span {
                height: 30px;
                padding: 0 12px;
                margin-top: 10px;
                line-height: 30px;
                cursor: pointer;
                float: left;

                &.active {
                    background: #D8D8D8;
                    color: #8F8F8F;
                    border-radius: 8px;
                }
            }

            >.data-box {
                float: left;
                margin-top: 10px;
                margin-left: 60px;

                >.ant-calendar-picker {
                    width: 230px;
                }
            }

            >.sortName {
                border: 1px solid #ccc;
                padding: 0 10px;
                border-radius: 2px;
                background: #fff;
            }

            >button {
                width: 100px;
                height: 35px;
                background: #dbdee2;
                border: 1px solid #eee;
                outline: none;
                border-radius: 4px;
                float: right;
                margin-right: 20px;
                margin-top: 10px;
            }
        }

        .state-box-select {
            width: 100%;
            height: 180px;

            >.select-item {
                float: left;
                width: 350px;
                margin: 20px;

                >.ant-calendar-picker {
                    width: 220px;
                }
            }

            >.select-btn {
                width: 200px;
                position: absolute;
                right: 50px;
                top: 100px;

                >button {
                    margin: 0 15px;
                }

            }
        }
    }

    .top-nav {
        width: 100%;
        height: 50px;
        background: #f7f7f7;
        line-height: 50px;
        border-bottom: 1.5px solid #dbdee2;

        >span {
            height: 100%;
            margin: 0 25px;
            display: inline-block;

            &.active {
                border-bottom: 2px solid #F48530;
                color: #000;
                font-weight: 600;
            }
        }
    }

    .ant-table-thead {
        width: 100%;

        >tr>th {
            background: #c8dbf9;
        }
    }

    .title-box {
        width: 100%;
        height: 40px;

        >b {
            width: 5px;
            height: 60%;
            background: #00379A;
            float: left;
            margin: 8px 15px;
        }

        >h2 {
            height: 100%;
            line-height: 40px;
            float: left;
            color: #00379A;
            font-weight: 600;
        }
    }

    .stars-box {
        width: 100%;
        overflow: hidden;

        .stars-box-item {
            width: 18%;
            margin: 10px;
            float: left;

            >.img-box {
                width: 100%;
                height: 150px;
                background: url("../../static/img/timg.jpg") no-repeat;
                background-size: contain;
                background-position: 67% 50%;
                position: relative;

                >img {
                    width: 90px;
                    height: 100px;
                    border-radius: 50%;
                    position: absolute;
                    left: calc(50% - 45px);
                    top: calc(50% - 50px);
                }

                >p {
                    width: 100%;
                    text-align: center;
                    position: absolute;
                    bottom: 0;

                    >span {
                        padding: 2px 15px;
                    }
                }
            }

            >.con {
                width: 100%;

                >h2 {
                    text-align: center;
                    font-size: 20px;
                    margin: 0;
                }

                >P {
                    text-align: center;
                    color: #BABABA;
                    margin: 0;
                    font-size: 14px;
                }

                >b {
                    text-align: center;
                    font-size: 28px;
                    color: #00379A;
                    display: block;
                }
            }
        }
    }
}